<!-- 组件说明 -->
<template>
    <div class='tab-control'>
        <div v-for="(item,index) in titles" :key="index" class='tab-control-item' :class="{active:index===currentIndex}" @click="itemClick(index)">
            <span>{{item}}</span>
        </div>
    </div>
</template>

<script>
//import x from ''
export default {
    name: 'TabControl',
    props: {
        titles:{
            type:Array,
            default(){
                return []
            }
        }
    },
    components: {


    },
    data () {
        return {
            currentIndex:0
        };
    },
    computed: {

    },
    methods: {
        itemClick(index){
            this.currentIndex=index;
            this.$emit('tabClick', index)
        }

    },
    mounted() {

},
    }
</script>

<style lang='' scoped>
.tab-control{
    display: flex;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    z-index: 9;
    background-color: #fff;
}
.tab-control-item{
    flex: 1;
}
.tab-control-item span {
padding: 5px;
}
.active{
    color: var(--color-high-text);
}  
.active span {
    border-bottom: 2px solid var(--color-high-text);
  }

</style>


